
#headercontainer {
	position: relative;
	z-index: 100;
}

#masthead {
	width: 100%;
	position: relative;
	padding: 15px;

	/*
	 * If class name "sticky" added to #masthead
	 * apply sticky rules
	 */
	&.sticky {
		z-index: 10200;
		transition: all .3s;
		position: fixed;

		@include is_scrolled {
			top: 0 !important;
		}

		@include is_admin_bar {
			top: 32px !important;
		}
	}

	&.layout-styles {

		/*
		 * styles includes 
		 * _header_style-01 _header_style-XX and so on - add more own styles if needed
		 * $_toggler_style_num: '01';
		 */
		
		&__s01 { @import "_header_style-01"; }
		/*
		&__s02 { @import "_header_style-02"; }
		&__s03 { @import "_header_style-03"; }

		*/
	}
}
