@import "_mixins";

body {
    font-family: $_font_base, sans-serif;
    @include fluid-type($min_width, $max_width, 16px, 19px);
    line-height: $_line_height_desk;
    font-weight: 400;
    color: $_color_txt;
}

h1 {
    @include fluid-type($min_width, $max_width, 26px, 34px);
}

h2 {
    @include fluid-type($min_width, $max_width, 24px, 32px);
}

h3 {
    @include fluid-type($min_width, $max_width, 22px, 28px);
}


h1,h2,h3,h4,h5,h6 {
    color: $_color_txt;
}

* + h1, * + h2, * + h3, * + h4, * + h5, * + h6 {
    margin-top: 15px !important;
}


hr {
    margin: 15px !important;
}


article {

    &.page {

        .entry-content {
            
            a {
                color: $_color_txt;
                text-decoration: none;

                &:hover {
                    text-decoration: underline;
                }
            }

            p {
                margin-bottom: 15px;
            }
            
        }
    }
}