
.link-section {
	padding: 0;
	background-size: cover;
	background-attachment: fixed;
	background-size: cover;
	background-color: $_dark_bg;



	&.gradiented {
		background: rgba(50,52,148,1);
		background: -moz-linear-gradient(45deg, rgba(50,52,148,1) 0%, rgba(231,86,123,1) 100%);
		background: -webkit-gradient(left bottom, right top, color-stop(0%, rgba(50,52,148,1)), color-stop(100%, rgba(231,86,123,1)));
		background: -webkit-linear-gradient(45deg, rgba(50,52,148,1) 0%, rgba(231,86,123,1) 100%);
		background: -o-linear-gradient(45deg, rgba(50,52,148,1) 0%, rgba(231,86,123,1) 100%);
		background: -ms-linear-gradient(45deg, rgba(50,52,148,1) 0%, rgba(231,86,123,1) 100%);
		background: linear-gradient(45deg, rgba(50,52,148,1) 0%, rgba(231,86,123,1) 100%);
		filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#323494', endColorstr='#e7567b', GradientType=1 );
		padding: 45px 0;
	}


	.widget-title {
		color: #fff;
		font-weight: 400;
		letter-spacing: 5px;
		font-size: 16px !important;
		text-align: center !important;
	}
	
	.textwidget {
		text-align: center;

		a {
			@include fluid-type($min_width, $max_width, 24px, 40px);
			color: #fff !important;
			position: relative;
			@include d-inline-block;
			padding: 0 30px;

			i {
				position: absolute;
				opacity: 0;
				transform: translateY(-35%);
				transition: all .45s ease-in;
				font-size: 20px;
				right: 5px;
				top: 50%;

				@include screen(custom, max, 767) {
					display: none;
				}
			}

			&:hover {
				i {
					opacity: 1;
					right: 0;
				}
			}
		}
	}

	&.black {
		background-size: cover;

		.overlay {
			padding: 75px 0;
			background: rgba(0,0,0,.85);
		}

		.textwidget {
			a {
				color: #fff;
			}
		}


	}
}