#page-top-image {
	background-size: cover;
	background-color: $_light_bg;
	background-position-x: center !important;
	background-repeat: no-repeat;


	.opacity-lvl {
		background: rgba(0,0,0,.65);
		padding: 8% 0;

		@include screen(custom, max, 992) {
			padding: 120px 0;
		}
	}

	.section-title,
	.breadcrumbs-wrapper {
		text-align: center;
		font-weight: 700;
	}

	.section-title {
		@include fluid-type($min_width, $max_width, 26px, 48px);
		text-transform: uppercase;
		letter-spacing: 3px;
		color: #fff;

		span {
			@include d-inline-block;
		    white-space: pre;
		    transform-origin: 50% -50%;
		}
	}

	.breadcrumbs-wrapper {
		font-size: 18px;
		color: #fff;

		a {
			color: #fff;
		}
	}

}