@mixin border-radius($rad) {
	-webkit-border-radius: $rad;
	-moz-border-radius: $rad;
	-ms-border-radius: $rad;
	-o-border-radius: $rad;
	border-radius: $rad;
}

// Button Style
@mixin btn-rounded {
	@include d-inline-block;
	border-radius: 60px;
	font-weight: bold;
	font-size: 14px;
	text-align: center;
	color: $main_text_color;
	border: 2px solid $border-color
}

@mixin btn-squared {
	@include d-inline-block;
	border-radius: 0;
	font-weight: bold;
	font-size: 14px;
	text-align: center;
	color: $main_text_color;
	border: 2px solid $border-color	
}

@mixin btn-rounded-hover {
	@include d-inline-block;
	border-radius: 60px;
	font-weight: bold;
	font-size: 14px;
	text-align: center;
	color: $placeholder_text_color !important;
	background-color:$main_text_color;
	border: 2px solid $main_text_color;
	text-decoration:none !important;
}

@mixin btn-squared-hover {
	@include d-inline-block;
	border-radius: 0;
	font-weight: bold;
	font-size: 14px;
	text-align: center;
	color: $placeholder_text_color !important;
	background-color:$main_text_color;
	border: 2px solid $main_text_color;
	text-decoration:none !important;
}

@mixin btn-large {
	padding: 10px 15px
}

@mixin d-flex {
	display: -webkit-flex;
	display: flex
}

@mixin flex-wrap($value: nowrap, $force:true) {
  // No Webkit/FF Box fallback.

  @if $force {
    -webkit-flex-wrap: $value !important;
    @if $value == nowrap {
      -ms-flex-wrap: none !important;
    } @else {
      -ms-flex-wrap: $value!important;
    }
    flex-wrap: $value !important;
   }
  @else {
    -webkit-flex-wrap: $value;
    @if $value == nowrap {
      -ms-flex-wrap: none;
    } @else {
      -ms-flex-wrap: $value;
    }
    flex-wrap: $value;
  } 
}

@mixin flex-direction($direction, $force: false) {
  /*
  flex-direction: row | row-reverse | column | column-reverse;
  */
  @if $force {
    -webkit-flex-direction: $direction !important;
    flex-direction: $direction !important;
  }
  @else {
   -webkit-flex-direction: $direction;
   flex-direction: $direction;
  }
}

@mixin align-content($direction, $force: false) {
/*
vertically align content inside flex
align-content: flex-start | flex-end | center | space-between | space-around | stretch;
*/
  @if $force {
    -webkit-align-content: $direction !important;
    align-content: $direction !important;
  }
  @else {
    -webkit-align-content: $direction;
    align-content: $direction;
  }
}

@mixin flex-align($direction, $force: false) {
	/*
  vertically
	align-items: flex-start | flex-end | center | baseline | stretch;
	*/
  @if $force {
    -webkit-align-items: $direction !important;
    align-items: $direction !important;
  }
  @else {
    -webkit-align-items: $direction;
    align-items: $direction;
  }
}

@mixin flex-justify($direction, $force: false) {
	/*
  horizontally
	justify-content: flex-start | flex-end | center | space-between | space-around | space-evenly;
	*/

  @if $force {
    -webkit-justify-content: $direction !important;
    justify-content: $direction !important;
  }
  @else {
    -webkit-justify-content: $direction;
    justify-content: $direction;
  }
}


@mixin d-inline-block {
	display: inline-block;
	vertical-align: middle
}

@mixin border-radius($radius) {
  -webkit-border-radius: $radius;
     -moz-border-radius: $radius;
      -ms-border-radius: $radius;
          border-radius: $radius;
}

