section {
	padding: 75px 0 0 0;

	@include screen(custom, max, 767) {
		padding: 25px 0;
	}

	.widget-title {
		text-transform: uppercase;
		font-weight: 700;
		text-align: center;
		color: $_color_light_txt;
		font-size: 16px;
		letter-spacing: 3px;
		transition: all .6s ease-in;

		@include screen(custom, max, 1280) {
			font-size: 15px;
		}
	}

	&.inview {
		.widget-title {
			letter-spacing: 5px;
		}
	}
}