#footercontainer:not(.main-footer) {
	//position: fixed;
	background: #fff;
	width: 100%;
	//bottom: 0;
	//z-index: 5;
	font-size: 16px;
	padding: 0;
	overflow: hidden;

	@include screen(custom, max, 1280) {
		font-size: 15px;
	}

	.menu-products-services-container {
		margin-bottom: 45px;
	}

	@include screen(custom, max, 992) {
		position: relative;
		bottom: auto;
	}


	#swifty-img-widget-8{
		.widget-title{
			visibility: hidden;
		}
	}

	#map {
		position: relative;

		#close {
			position: absolute;
			top: 15px;
			right: 15px;
			cursor: pointer;

			@include screen(custom, max, 992) {
				display: none;
			}

			i {
				color: #ff0000;
				font-size: 36px;
			}
		}
	}

	.site-footer {
		padding: 75px 0 0;


	}



	a {
		color: $_color_txt;
	}

	#custom_html-15 {
		small {
			display: block;
		}
	}

	.swifty_imgwidget_ul {
		padding: 0;
		list-style: none;
	}

	#swifty-img-widget-7 {
		@include screen(custom, max, 992) {
			margin-bottom: 55px;
		}

		li {

			img {
				margin: 15px 0;
			}

			a[href*="map"] {
				font-weight: bold;
			}
		}


		img {
			filter: grayscale(100%);
			margin-bottom: 15px;
			transition: all .3s ease-in;

			&:hover {
				filter: grayscale(0);
			}
		}
	}

	.widget-title {
		text-align: left;
		margin: 0 0 30px 0;
	}

	.menu {
		margin: 0;
		padding: 0;
		list-style: none;

		li {
			overflow: hidden;
			a {
				display: block;
				padding: 3px 0;
				transition: all .3s;

				&:hover {
					padding-left: 3px;
				}

				i {
					font-size: 13px;
					@include d-inline-block;
					width: 20px;
					margin-right: 8px;
					text-align: center;
					float: left;
					line-height: 2.3;
				}
			}
		}
	}
}

#footercontainer.main-footer {
	background: #fff;
	padding: 75px 0 10px;

	.container-fluid {
		max-width: 1270px;
	}

	aside#no-icons {
		i {
			display: none;
		}
	}

	.widget-title {
		font-family: $_font_heading;
		letter-spacing: normal;
		@include fluid-type($min_width, $max_width, 26px, 56px);
		text-transform: uppercase;
		color: $_color_txt;
		text-align: left;
		font-weight: 400;
		margin-top: 15px;
	}

	.footer-logo {
		display: block;
		width: 100%;
		max-width: 320px;
		min-width: 260px;
		margin: 0 auto 65px;
		flex: auto;
	}

	.footer-block {

		&.footer-menu-1 {
			margin-left: 100px;
		}

		.menu {
			margin: 0;

			li {

				a {
					text-align: left;
					font-family: $_font_heading;
					text-transform: uppercase;
					@include fluid-type($min_width, $max_width, 17px, 22px);

					&:hover {
						text-decoration: underline;
					}				
				}
			}
		}
	}
}

#copy {
	//transform: translateY(-100%);
	margin: 55px auto 0;

	@include screen(custom, max, 992) {
		transform: translateY(0);
	}

	p {
		margin: 10px 0;
		padding: 20px 0;
		text-align: center;
		font-size: 16px;
		text-transform: uppercase;
		position: relative;
		font-family: $_font_heading;

		&:before {
			content: '';
			display: block;
			width: 165px;
			height: 3px;
			background: $_color_txt;
			transition: all .3s;
			position: absolute;
			left: 50%;
			transform: translateX(-50%);
		}

		&:before {top: 0;}
		
	}

	&:hover {

		p {

			&:before {
				width: 100%;
				left: 0;
				transform: translateX(0);
			}
		}
	}
}