.sectional-title {
	@include fluid-type($min_width, $max_width, 32px, 90px);
	font-weight: normal;
	text-transform: uppercase;
	line-height: 1;
	padding: 0;
	margin: 0 0 36px 0;
	position: relative;
	font-family: $_font_base_narrow;

	&:before {
		content:'';
		display: block;
		position: absolute;
		height: 60%;
		width: 100%;
		background: #000;
		right: -100%;
		top: 10px;
		//transform: translateY(-50%);
		transition: all .65s ease-in;
		opacity: 0;
	}

	.sectional-description {
		@include fluid-type($min_width, $max_width, 15px, 20px);
		text-transform: uppercase;
		padding-left: 0.3%;
		font-family: $_font_heading;
	}

	&.inview {

		&:before {
			right: 103%;
			opacity: 1;
		}				
	}

}