body {
	color: $_dark_bg;
	position: relative;
	min-width: 320px;

	p.tooltip {
		position: absolute;
		display: block;
		padding: 2px 5px;
		background: $_color_txt;
		color: #fff;
		font-family: $_font_heading;
		@include fluid-type($min_width, $max_width, 16px, 20px);
		z-index: 100;
		font-weight: 300;
		transform: translateX(-50%);
		transition: all .3s;
	}
}

.flexme {
	@include d-flex;
	@include flex-direction(row);
	@include align-content(center);
	@include flex-align(center);
	@include flex-wrap(wrap);
	@include flex-justify(space-between);
}